
.portfolio-piece {
  position: relative;
  margin:auto;
  width:100%;
  max-width: 750px;
  /*height: 425px;*/
}

.portfolio-piece a:hover {
  color:#101010;
  text-decoration: none;
}

.portfolio-piece .piece-title {
  /*position: absolute;*/
  /*top: 0;*/
  /*left: 0;*/
  /*margin: 30px 0 0 30px;*/
  /*font-size: 72px;*/
  /*line-height: initial;*/
  /*font-weight: 700;*/
  font-size:18px;
  /*text-decoration:underline;*/
  color:#101010;
  margin:0.4em 0;
}

.portfolio-piece .piece-description {
  /*position:absolute;*/
  /*left:0;*/
  /*bottom:0;*/
  /*margin: 30px 30px;*/
  /*font-size:24px;*/
  font-weight:300;
  line-height:1.3em;
  opacity:0;
  color:#101010;

  -webkit-transition: opacity 0.5s;

  -o-transition: opacity 0.5s;

  transition: opacity 0.5s;
}

.portfolio-piece .piece-description div {
  /*margin-top:0.5em;*/
}

.portfolio-piece:hover .piece-description {
  opacity:1.0;
}

/*.portfolio-piece:hover .piece-description {
  opacity:1.0;
}*/

.portfolio-piece .piece-image {
  overflow: hidden;
  position: relative;
  width: 100%;
  height: 100%;
  background-color: #ffffff;
  -webkit-transition: background-color 0.5s;
  -o-transition: background-color 0.5s;
  transition: background-color 0.5s;
}

/*.portfolio-piece:hover .piece-image {
  background-color:#000;
}*/

.portfolio-piece .piece-image img {
  left: 0;
  /*position: absolute;*/
  opacity:1.0;
  -webkit-transform: scale(1.0);
      -ms-transform: scale(1.0);
          transform: scale(1.0);
  /*min-width:750px;*/

  -webkit-transition: opacity 0.75s, -webkit-transform 3s;

  transition: opacity 0.75s, -webkit-transform 3s;

  -o-transition: transform 3s, opacity 0.75s;

  transition: transform 3s, opacity 0.75s;

  transition: transform 3s, opacity 0.75s, -webkit-transform 3s;
}

.portfolio-piece .piece-image img {
  width:100%;
}

.portfolio-piece:hover .piece-image img {
  -webkit-transform: scale(1.1);
      -ms-transform: scale(1.1);
          transform: scale(1.1);
  /*opacity:0.8;*/

  -webkit-transition: opacity 0.75s, -webkit-transform 8s;

  transition: opacity 0.75s, -webkit-transform 8s;

  -o-transition: transform 8s, opacity 0.75s;

  transition: transform 8s, opacity 0.75s;

  transition: transform 8s, opacity 0.75s, -webkit-transform 8s;
}

.portfolio-piece .ext-link {
  font-size: 14px;
  opacity:0.5;
}

.portfolio-piece .ext-link {
  color:#1a1a1a;
}

@media only screen and (max-width : 480px) {

  .portfolio-piece .piece-title {
    /*margin: 30px 0 0 30px;*/
    /*font-size: 48px;*/
  }

  .portfolio-piece .piece-image img.fit {
    width:auto;
  }

  .portfolio-piece .piece-image img {
    height:100%;
    min-width:0;
  }

  .portfolio-piece .piece-image img.voxeldreams {
    left:-135px;
  }

  .portfolio-piece .piece-image img.tvrn {
    left:-255px;
  }

  .portfolio-piece .piece-image img.etrade {
    left:-210px;
  }

  .portfolio-piece .piece-image img.novus {
    left:-400px;
  }

}



